/*  weather container */
main {
	width: 100%;
	padding: 10px;
	border-radius: 1em;
	font-family: "Poppins";
}

.weather-container {
	width: 100%;
	display: flex;
	flex-wrap: wrap;
	flex-direction: row;
	gap: 10px;
	padding-bottom: 10px;
}

.display {
	font-size: 28px;
}

.display.error {
	color: #d2001a;
}

/* Glassmorphism */
.glass {
	background: rgba(196, 205, 228, 0.26);
	border-radius: 16px;
	box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
	backdrop-filter: blur(13.7px);
	-webkit-backdrop-filter: blur(13.7px);
	border: 1px solid rgba(196, 205, 228, 0.11);
}

/* Main Weather Information */
.main-weather {
	flex: 1 0 215px;
	padding: 10px;
	border-radius: 1em;
}

.main-weather .weather-place {
	font-size: 24px;
	font-weight: 700;
}

.main-weather .upper-main {
	display: flex;
	align-items: center;
	flex-direction: column;
}

.main-weather .logo-weather {
	width: 125px;
	height: 125px;
}

.main-weather .logo-weather img {
	width: 100%;
}

.main-weather .main-temp {
	font-size: 52px;
	max-height: 325px;
}

.main-weather .weather-place,
.main-weather .weather-date {
	padding: 4px 10px;
}



/* sun information */
.sun-information {
	padding: 10px;
	border-radius: 1em;
	display: flex;
	justify-content: center;
}

.sun-information div {
	padding: 0 20px;
	display: flex;
	align-items: center;
	flex-wrap: wrap;
	align-content: center;
}

.sun-information > div {
	font-weight: 650;
}

/* other-information */
.other-information {
	width: 100%;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-content: space-between;
	gap: 10px;
	flex: 1 0 415px;
}

.other-information > div.information {
	padding: 10px;
	border-radius: 1em;
	flex: 1 0 250px;
	height: 185px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.information-text {
	font-weight: 650;
}

/* Forecast Containers*/
.forecast-container {
	margin-top: 10px;
}

.hourly-container,
.daily-container {
	padding: 0 10px;
	margin-bottom: 10px;
}

.hourly-header,
.daily-header {
	font-weight: 650;
}

.container-hourly-scroller,
.container-daily-scroller {
	overflow-x: hidden;
	white-space: nowrap;
}

.hourly-forecast,
.daily-forecast {
	display: flex;
	justify-content: space-between;
	gap: 10px;
}

.hourly-weather,
.daily-weather {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.hourly-weather img,
.daily-weather img {
	width: 125px;
	margin: 5px 0;
}

.hourly-weather > p,
.daily-weather > p {
	font-weight: 750;
	margin: 6px 0;
}



@media screen and (max-width: 767px) {
	.display {
		font-size: 17px;
	}
	.weather-container {
		flex-direction: column;
	}
	.main-weather .logo-weather {
		width: 100px;
		height: 100px;
	}

	.main-weather .main-temp {
		font-size: 42px;
	}
	.main-weather .weather-name p {
		font-size: 14px;
		margin: 8px 0;
	}
	.other-information {
		gap: 8px;
		flex: 0 0;
	}

	.other-information > div.information {
		flex: 1 0 45%;
		height: 140px;
	}

	.information img {
		width: 45px;
		height: 45px;
	}
	.information-text,
	.information-value {
		font-size: 14px;
	}

	.sun-information {
		padding: 8px;
	}

	.sun-information div {
		padding: 0 4px;
		font-size: 12px;
	}

	.hourly-header,
	.daily-header {
		font-size: 14px;
		margin: 10px 0 8px 0;
	}
	.hourly-forecast,
	.daily-forecast {
		cursor: grab;
		justify-content: space-between !important;
		margin: 0;
	}

	.hourly-forecast:active,
	.daily-forecast:active {
		cursor: grabbing;
	}

	.hourly-weather,
	.daily-weather {
		padding: 5px;
	}

	.hourly-weather img,
	.daily-weather img {
		width: 70px;
	}

	.hourly-weather > p,
	.daily-weather > p {
		font-size: 12px;
		margin: 4px 0;
	}
}


@media screen and (min-width: 768px) and (max-width: 1024px) {
	.display {
		font-size: 20px;
		display: flex;
		justify-content: center;
	}
	.weather-container {
		flex-direction: column;
	}
	.main-weather {
		flex: 1 0 0;
		padding:8px 25px;
		border-radius: 1em;
	}

	.main-weather .main-temp {
		padding: 8px;
	}

	.main-weather .weather-name p{
		margin: 0 0 8px 0;
	}

	.other-information {
		flex: 0 0;
	}

	.other-information > div.information {
		flex: 1 0 45%;
		height: 150px;
	}

	.hourly-weather img,
	.daily-weather img {
		width: 100px;
	}


}