header {
	width: 100%;
	height: 60px;
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin-bottom: 15px;
	font-family: "Poppins";
	padding: 0 10px;
}
.logo {
	cursor: pointer;
}
header .search-bar {
	width: 50%;
	height: 100%;
	background: rgba(196, 205, 228, 0.26);
	border-radius: 16px;
	box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
	backdrop-filter: blur(13.7px);
	-webkit-backdrop-filter: blur(13.7px);
	border: 1px solid rgba(196, 205, 228, 0.11);
	padding: 5px;
	transition: 0.1s;
}

header form {
	width: 100%;
	height: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 5px;
}

header .search-bar:focus-within {
	border: 3px solid #498ee7;
}

header form .search-icon {
	width: 50px;
	height: 35px;
	background-color: inherit;
	border: none;
	cursor: pointer;
}

header form .search-icon img {
	width: 40px;
	height: 35px;
}

header form input {
	width: 100%;
	height: 100%;
	font-size: 18px;
	font-weight: 600;
	border: none;
	background-color: inherit;
	transition: 0.5s;
}

header form input::placeholder {
	font-size: 16px;
	font-weight: 550;
	color: rgba(0, 0, 0, 0.5);
}

header form input:focus {
	outline: none;
	border: none;
}

header img.delete {
	width: 40px;
	height: 40px;
	padding: 5px;
	cursor: pointer;
}

header .temperature {
	width: 100px;
	height: inherit;
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.temperature button {
	height: inherit;
	width: 50%;
	border: none;
	background: rgba(196, 205, 228, 0.26);
	box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
	backdrop-filter: blur(13.7px);
	-webkit-backdrop-filter: blur(13.7px);
	border: 1px solid rgba(196, 205, 228, 0.11);
	padding: 5px;
	font-family: "Poppins";
	font-size: 16px;
	font-weight: 650;
	cursor: pointer;
	transition: 0.3s;
}

.temperature .celsius {
	border-radius: 16px 0 0 16px;
	border-right: 1px solid rgba(0, 0, 0, 0.1);
}

.temperature .fahrenheit {
	border-radius: 0 16px 16px 0;
	border-left: 1px solid rgba(0, 0, 0, 0.1);
}

.temperature button.active {
	background-color: #56ccf2;
}

.temperature button:not(.active):hover {
	background: rgba(196, 205, 228, 0.6);
}

@media screen and (max-width: 767px) {
	header {
		margin-bottom: 15px;
		padding: 0 5px;
	}

	.logo h1 {
		font-size: 18px;
	}

	header .search-bar {
		width: 50%;
		height: 75%;
		border-radius: 8px;
	}

	header form .search-icon {
		width: 30px;
		height: 100%;
		padding: 2px;
		display: flex;
		align-items: center;
	}

	header form .search-icon img {
		width: 25px;
		height: 25px;
	}

	header form input {
		font-size: 14px;
		margin-bottom: -2px;
	}

	header img.delete {
		width: 30px;
		height: 30px;
		padding: 3px;
	}

	header .temperature {
		width: 70px;
		height: 85%;
	}

	.temperature .celsius {
		border-radius: 8px 0 0 8px;
		font-size: 14px;
	}

	.temperature .fahrenheit {
		border-radius: 0 8px 8px 0;
		font-size: 14px;
	}
}

@media screen and (max-width) {
	header {
		margin-bottom: 15px;
		padding: 0 10px;
	}
}